import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import { Helpers } from '../../../../../helpers';
import { ScriptLoaderService } from '../../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../animations/round-anticlock.animation';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { MatInputModule, MatNativeDateModule, MatSelectModule, MatSort, PageEvent} from '@angular/material';
import {CdkTableModule} from '@angular/cdk/table';
import {DataSource} from '@angular/cdk/collections';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {Observable} from 'rxjs/Observable';
import {ListComponent, SafeHtmlPipe} from '../../../list/list.component'
import {FormControl, Validators, FormGroup} from '@angular/forms';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute} from '@angular/router';

@Component({
selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
templateUrl: "./sms.component.html",
encapsulation: ViewEncapsulation.None,
})

export class SmsComponent implements OnInit, AfterViewInit {
	
	smsId = 0;  
	smsForm: FormGroup;  
	sms = new smsData();
	grandTotal: string;
	listHide: boolean = true; 
	bootstrapFromValues = []; 
	smsSummaryData = []; 
	reporttypes = [
		{key:'1', name:'Contract Details'}
		
	]; 
	 exportformats = [
		{key:'1', name:'MS Excel'},
		 {key:'12', name:'HTML'}
	];
	
constructor( private http: HttpClient, private _router: Router )  { }
	
ngOnInit()  { 
	
//		this.populateDropDownData();
		this.getTodaysDate();
//		this.populateData();
		if(this.smsSummaryData.length > 0){
			  this.listHide = false;
		}  
		this.smsForm = new FormGroup({	
			'branchsIds' : new FormControl(''),
			'exportformat' : new FormControl(''),
			'reporttype' : new FormControl(''),
		});
}
	
		get exportformat(){ return this.smsForm.get('exportformat')};
	    get reporttype(){ return this.smsForm.get('reporttype')};
	    get branchsIds(){ return this.smsForm.get('branchsIds')};
	 
	 
	   populateDropDownData(){
		  const headers = new HttpHeaders({'Content-Type':'application/json'});
		  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/branch',{},
									{ headers, withCredentials: true }).subscribe(data =>{
//				  														this.branches = data['populateBranch'];
			  															});
	  }
	
	
     ngAfterViewInit(){
		  $.getScript('assets/app/js/bootstrap-material-datetimepicker.js',function(){
		   });
	 }

	 getTodaysDate(){
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/currentdate',JSON.stringify({ requestType : 'shortDate' }),
				{ headers, withCredentials: true }).subscribe(data =>{
								this.sms.startDate= data['date'];
								this.sms.endDate= data['date'];
				});
	 }
	
	populateData(){
		  this.sms['requestType'] = 'landing';
		   const headers = new HttpHeaders({'Content-Type':'application/json'});
		   this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/smssummary',JSON.stringify(this.sms),
								{ headers, withCredentials: true }).subscribe(data =>{
			   					this.smsSummaryData = data['populateData'];
			   					this.grandTotal = data['grandTotal'];
				}); 
	  }
	
	
	
	onSubmit(){
		   this.listHide = false;
		  this.bootstrapFromValues = $('#m_form_1').serializeArray();
			for(var i=0;i<this.bootstrapFromValues.length;i++){
				this.sms[this.bootstrapFromValues[i].name]=this.bootstrapFromValues[i].value;
			}
		console.log(this.sms);
		 this.sms['requestType']='landing';
		    const headers = new HttpHeaders({'Content-Type':'application/json'});
		   this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/sms',
								 JSON.stringify(this.sms),
								{ headers, withCredentials: true }).subscribe(data =>{
			   					this.smsSummaryData = data['populateLandingDetails'];
			   					this.grandTotal = data['grandTotal'];
					}); 
	  }
}


export class smsData
{
	startDate: string='';
	endDate: string='';
	branchsIds: string;
	exportformat: string;
	reporttype: string;
}



