import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import { Helpers } from '../../../../../helpers';
import { ScriptLoaderService } from '../../../../../_services/script-loader.service';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../animations/round-anticlock.animation';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../animations/fly-in-out.animation';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { MatInputModule, MatNativeDateModule, MatSelectModule, MatSort, PageEvent} from '@angular/material';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import {CdkTableModule} from '@angular/cdk/table';
import {DataSource} from '@angular/cdk/collections';
import {ListComponent, SafeHtmlPipe} from '../../../list/list.component'
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {Observable} from 'rxjs/Observable';
import { FormControl, Validators, FormGroup, ValidationErrors } from '@angular/forms';

import 'rxjs/add/operator/filter' ;
import { ToastComponent } from '../../../../layouts/toast-notification/toast.component';


@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./sms-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class SmsAddComponent    {
    @HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ToastComponent) toast: ToastComponent;
	
	 smsForm: FormGroup;
     bootstrapFromValues = [];
	 sms= new smsData();
	 configDetails = new ConfigDetails();
	 targetId = "0";
	 msg = [];
 	 branch = [];
	 target = [
		 {key:"1",name:"All Contracts"},
		 {key:"2",name:"All CUstomers"},
		 {key:"3",name:"All Suppliers"}
	 ];
	 
   constructor(private http: HttpClient, private _router: Router, private route:ActivatedRoute) { 
   		this.route.queryParams.filter(params => params.targetId).subscribe(params=>{
			if(params.targetId != undefined){
				this.targetId = params.targetId; 
				}
		});
		
		if(this.targetId == "1")
   			this.sms['smsTarget'] = "1";
   		
   		if(this.targetId == "2")
   			this.sms['smsTarget'] = "2";
   			
   		if(this.targetId == "3")
   			this.sms['smsTarget'] = "3";		
     }
	 
	 ngOnInit() {
		this.smsForm = new FormGroup({	
			'smsMsg' : new FormControl(''),
			'smsTarget' : new FormControl(''),
			'smsBranchId': new FormControl(''),
		});
		
		//this.populateDropDownData();
	 }
	 
	 get smsMsg(){ return this.smsForm.get('smsMsg')};
	 get smsTarget(){ return this.smsForm.get('smsTarget')};
	 get smsBranchId(){ return this.smsForm.get('smsBranchId')};
	 
	 populateDropDownData(){
		 
		  const headers = new HttpHeaders({'Content-Type':'application/json'});
		  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/branch',{},
								{ headers, withCredentials: true }).subscribe(data =>{
																	this.branch = data['populateBranch']
																	});
		 
		  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/contact',{},
								{ headers, withCredentials: true }).subscribe(data =>{
																	this.target = data['populateContactTitle']
																	});
		 
		 this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/configdetails',{},
									{ headers, withCredentials: true }).subscribe(data =>{
				  													this.configDetails = data['populateconfigdetails'][0];
			  															},
															 (err) => {
																	console.log("ERROR: "+err);
															 },
															 () => {
															 });	
	 }
}
			
			
			
export class smsData
{
	smsId: string;
	smsBranchId: string;
	smsTarget: string;
	smsContactId: string;
	smsContact: string;
	smsLeadId: string;
	smsLead: string;
	smsEmpId: string;
	smsEmp: string;
	smsMobileno: string;
	smsMsg: string;
	smsDate: string;
	smsEntryId: string;
	smsPriority: string;
	smsCredits: string;
	smsSent: string;
}

export class ConfigDetails {
    configCustomerName:string;
}


